@media screen and (max-width: 750px) {
    .index {
        .container {
            max-width: 640px;
            padding: 0 4.63%;
        }

        main {
            font-size: 10px;
        }

        .main_visual {
    
            margin-top: 149px;
            height: calc(100vh - 149px);
            max-height: 140vw;
            min-height: 400px;
         

            &::before {
                max-width: 85%;
                width: 85%;
                height: 85vw;
                bottom: 10%;
            }

            .key {
                padding: 5%;
            }

            .idx_box {
                max-width: 550px;
                margin-left: 10px;
                height: auto;
                padding: 3em 10px;

                .sub {
                    width: 100%;
                    font-weight: 300;
                    font-size: 1.2em;
                }

                .flex_col3 p {
                    font-size: 2em;
                    padding: 0px 20px;
                }
            }

            .pic1 {
                position: relative;
                z-index: 1;
                width: 50%;
              

                img {
                    max-height: 100%;
                }
            }

            .pic2 {
                position: absolute;
                right: 0;
                top: 68px;
                max-width: 41%;
            }

            .pic3 {
                position: absolute;
                right: 0px;
                bottom: 0px;
                max-width: 41%;
            }

            h2 {
                font-size: 3.4em;
                line-height: 1.382;
            }
        }

        h3 {
            text-align: center;
            font-size: 3.6em;

            &.st2 {
                .en {
                    margin-bottom: 12px;
                }

                &::after {
                    margin-top: 19px;
                }

                .sub {
                    font-size: 0.4166667em;
                }
            }

            .en {
                font-size: 0.833333em;
            }
        }

        h4 {
            font-size: 3em;
            line-height: 1.36667;
        }

        .txt {
            font-size: 2.6em;
            line-height: 1.84615;
            font-weight: 300;
        }

        .list {
            font-size: 2em;
            line-height: 1.35;
          
        }

        .idx_box {
            &.pic1{
                &::before {
                    max-width: 6.6em;
                    max-height: 6.6em;
                    bottom: -2.2em;
                    right: -2.2em;
                }
                &::after{
                    left: 1em;
                    bottom: 1em;
                }
            }

            &.pic2{
                &::before {
                    max-width: 5.6em;
                    max-height: 5.6em;
                    top: -2.2em;
                    left: -2.2em;
                }
                &::after{
                    left: 1em;
                    bottom: 1em;
                }
            }
        }

        .btn {
            margin-right: auto;
            margin-left: auto;
            max-width: 500px;
            width: 100%;
            height: auto;
            min-height: 50px;
            font-size: 2.6em;

            a {
                padding: 0.6em 10px;
                font-size: 100%;
                letter-spacing: 0;
            }
        }
    }
}

@media screen and (max-width: 750px) {
    .index01 {
        padding-top: 7px;
        padding-bottom: 51px;

        h4 {
            margin-bottom: 0px;
        }

        .idx01_row {
            display: table;
            margin: 13px auto 64px;

            p {
                padding: 1.5em 18px 0px 90px;
                font-size: 2.6em;
                line-height: 1.5384;

                .en {
                    top: 6px;
                    font-size: 5.538em;
                }

                &:nth-child(2) {
                    .en {
                        top: -6px;
                    }
                }

                &:nth-child(3) {
                    .en {
                        top: -16px;
                    }

                    margin-top: 29px;
                }
            }
        }

        .title_big {
            font-size: 10.6em;

            .sm {
                font-size: 84.9%;
            }
        }

        .key_page_num {
            font-size: 2.6em;
        }

        .key_btn {
            width: 2.6em;
            height: 2.6em;
            margin: 0px 2em;

            .c-pager-btn {
                background-size: auto 100%;
            }
        }

        .page_line {
            height: 1em;
            background-size: auto 100%;
        }

        .gr_nav {
            padding: 0;
        }

        .flex {
            display: block;
        }

        .box_slider {
            justify-content: space-between;

            .event-slide {
                height: auto;
                width: 81.2%;
                max-width: 470px;
            }

            .event-nav {
                width: 100px;
                max-width: 17.25%;
                height: auto;

                li {
                    padding: 0;
                }
            }
        }

        .box_left {
            margin-right: auto;
            margin-left: auto;
            margin-bottom: 7px;
        }

        .box_right {
            max-width: 596px;
            margin-right: auto;
            margin-left: auto;
        }
    }
}

@media screen and (max-width: 750px) {
    .index02 {
        padding-bottom: 6.1em;
        padding-bottom: 61px;

        &::before {
            top: auto;
            bottom: 0;
            height: 69%;
            background-image: url(../images/idx02_bgsp.jpg);
            background-size: cover;
            max-height: 636px;
        }

        .container {
            padding-top: 197px;
            flex-direction: column-reverse;
        }

        h3 {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
        }

        .box_text {
            margin: 0;
            max-width: 100%;

            .txt {
                margin-bottom: 38px;
                max-width: 100%;
            }
        }

        .box_img {
            margin-bottom: 23px;
            width: 100%;
            flex-direction: row;
            justify-content: space-between;

            >div {
                margin: 0;
                width: 48%;
                max-width: 280px;

                &.img2 {
                    margin-top: 30px;
                }
            }
        }
    }
}

@media screen and (max-width: 750px) {
    .index03 {
        padding: 80px 0px 100px;


        .container {
            display: block;
            padding-bottom: 61px;
            position: relative;
        }

        .box_tt {
            position: inherit;
            margin: 0 auto 30px;
            display: contents;

            .idx_box {
                display: block;
                width: 300px;
                transform: translateX(5px);
                padding: 33px 20px 27px;
                height: auto;
                margin: 0 auto;

                h3 {
                    padding-right: 5px;
                    margin: auto;
                    line-height: 1.05;
                }
            }

            .btn_more {
                margin-bottom: 0;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                width: calc(100% - 20px);
                max-width: 500px;
                font-size: 1em;

                a {
                    text-align: center;
                    font-size: 2.6em;

                    &::before {
                        right: calc(50% - 77px);
                    }
                }
            }
        }

        .idx_box {}

        .box_news {
            position: relative;
            width: 100%;
            max-width: 500px;
            margin: 35px auto 30px;

            li {
                font-size: 1em;

                a {
                    padding: 15px 0px;
                    flex-wrap: wrap;
                    font-size: 2em;
               
                }

                .cate {
                    width: 6em;
                    height: 2em;
                }

                .date {
                    margin-left: 20px;
                    width: auto;
                }

                .tt {
                    margin-top: 1em;
                    width: 100%;
                }
            }
        }
    }
}

@media screen and (max-width: 750px) {
    .index04 {
        padding: 402px 0px 27px;

        &::before {
            max-height: 495px;
            background-image: url(../images/idx04_bg2sp.jpg);
        }

        h3 {
            margin-bottom: 18px;
        }

        .idx04_box {
            padding: 47px 20px;

            .txt {
                max-width: 500px;
                margin: auto;
            }
        }

        .idx04_row {
            margin-bottom: 9px;

            .box_text {
                padding: 0;
                margin-top: 33px;
                width: 100%;

                h4 {
                    margin: 0px auto 33px;
                    font-size: 3.6em;
                }

                .txt {}
            }

            .box_img {
                top: auto;
                left: auto;
                margin-bottom: 30px;
                width: 100%;
                position: relative;
            }
        }

        .btn {
            margin-top: 0;
        }
    }
    @supports (-webkit-touch-callout: none) {
        .index04::before {
            background-attachment: scroll;
        }
    }
    @media not all and (min-resolution:.001dpcm) {
        @supports (-webkit-touch-callout: none) {
            .index04::before {
                background-attachment: scroll;
            }
        }
      
    }
}

