@media screen and (max-width: 750px) {
    .index06 {
        padding: 89px 0px 60px;

        h3.st2 {
            letter-spacing: 0;
            margin-bottom: 18px;

            .en {
                margin-bottom: 21px;
            }
        }

        .list::before {
            top: 0.55em;
        }

        .idx06_row {
            padding-bottom: 50px;
            margin-bottom: 0;

            &::before {
                height: 70%;
                top: 97px;
                left: 10%;
            }

            .idx_box {
                position: relative;
                width: 46%;
                max-width: 260px;
                padding-right: 10px;
                padding-top: 10px;

                &::before {
                    background-color: #EDF7F8;
                }

                &::after {
                    width: calc(100% - 10px);
                    height: calc(100% - 10px);
                }
            }

            &.row2 {
                &::before {
                    right: 20%;
                    left: auto;
                }

                .idx_box.pic2 {
                    width: 42%;
                    top: -345px;
                    max-width: 240px;
                    right: 0px;
                    position: absolute;

                    &::before {
                        top: auto;
                        bottom: -2.2em;
                    }
                }
            }
        }

        .pic {
            right: auto;
            top: auto;
            margin: 0px -10px 10px;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;

            .col {
                max-width: 280px;
                margin-left: 10px;
                margin-right: 10px;
                width: calc(50% - 20px);
                margin-bottom: 35px;

                &:nth-child(1) {
                    margin-left: 20%;
                    margin-right: 20%;
                }
            }
        }
    }
}

@media screen and (max-width: 750px) {
    .index07 {
        padding: 58px 0px 0px;
        &::before{
            background-position: top center;
            background-image: url(../images/idx07_bgsp.jpg);
        }
        h3.st2 {
            margin-bottom: 40px;
            &::after{
                top: 1.3em;
            }
            .en{
                margin-bottom: 1.5em;
            }
        }
        .idx07_row {
            justify-content: space-between;

            .box {
                margin: 0;
                width: 48.5%;
                max-width: 280px;
height: 20em;
max-height: 184px;
background-size: cover;
                &:nth-child(n + 3) {
                    margin-top: 20px;
                }

                &:last-child {
                    margin-left: auto;
                    margin-right: auto;
                }
              
                    .sub{
                        margin-bottom: 0.85em;
                        font-size: 2em;
                        line-height: 1.35;
                        &::after{
