@media screen and (max-width: 750px) {
    body.ovh #header, #header.active {
        padding-top: 10px;
        padding-bottom: 10px;
        height: 100px;
        align-items: center;
        display: flex;

        .inner {}

        .hamburger-btn {
            bottom: 10px;
        }

        .h1_txt {
            margin: 0;
            padding: 0;
            opacity: 0;
            height: 0;
            color: transparent;
        }

        #top {
            height: auto;
        }
    }

    #header {
        position: fixed;
        min-width: 100%;
        padding: 21px 30px 19px;
        height: 149px;
        z-index: 11;
        background-color: #fff;

        .h_right {
            height: 0;
        }

        .h1_txt {
            margin-top: 0px;
            margin-bottom: 1.5em;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }

    #top {
        margin: 0;
        width: 100%;

        a {
            display: block;
            max-width: 414px;
            width: calc(100% - 100px);
        }
    }

    .h_add, .kidsortho {
        display: none;
    }
}

@media screen and (max-width: 750px) {
    .menu_toggle {
        background-color: #fff;
        position: fixed;
        top: 100px;
        bottom: 0px;
        left: 0px;
        width: 100%;
        height: calc(100vh - 60px);
        padding: 0px 0px 0px;
        overflow: hidden;
        opacity: 0;
        visibility: hidden;
        transition: all 0.3s linear;
        z-index: 999;

        &.active {
            opacity: 1;
            visibility: visible;
        }

        ul {
            li {
                position: relative;
                overflow: hidden;
            }
        }

        p {
            margin-bottom: 0;
        }

        a, p {}

        &.active {}

        .inside {
            -webkit-overflow-scrolling: touch;
            /* Lets it scroll lazy */
            transition: all 0.5s ease;
            height: 100%;
            overflow: auto;

            .menu_list {
                margin: 0;
                padding-bottom: 120px;

                ul li {
                    font-size: 1em;

                    &:not(.menu_sub) {
                        a {
                            padding-left: 2.3em;

                            &:before {
                                content: "";
                                display: block;
                                position: absolute;
                                left: 1.1em;
                                bottom: 1.5em;
                                height: 1px;
                                width: 0.8em;
                                background-color: #333333;
                            }
                        }
                    }

                    &.menu_sub {
                        >a {
                            border-bottom: 1px solid #C29C61;
                        }

                        ul {}
                    }
                }

                >ul>li>a {
                    display: block;
                    margin: 0;
                    padding: 1.29em 1.1em 1.08em;
                    font-size: 2.6em;
                    color: #333333;
                    text-decoration: none;
                    font-family: $fnotoserif;
                    border-bottom: 1px solid #EFEFEF;
                    -webkit-transition: none !important;
                    -o-transition: none !important;
                    transition: none !important;
                }

                ul .menu_sub>a {
                    position: relative;
                    pointer-events: visible;
                    z-index: 2;

                    &:hover, &:active, &:focus {
                        opacity: 1;
                    }
                }

                ul .menu_sub>a::before, ul .menu_sub>a::after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    -webkit-transform: translateY(-50%);
                    -ms-transform: translateY(-50%);
                    transform: translateY(-50%);
                    background: #C29C61;
                    -webkit-transition: 0.3s all ease;
                    -o-transition: 0.3s all ease;
                    transition: 0.3s all ease;
                }

                ul .menu_sub>a::before {
                    right: 1em;
                    width: 0.77em;
                    height: 2px;
                }

                ul .menu_sub>a::after {
                    right: 1.3em;
                    width: 2px;
                    height: 0.77em;
                }

                ul .menu_sub ul {
                    display: none;
                    -webkit-transition: 0.3s all ease;
                    -o-transition: 0.3s all ease;
                    transition: 0.3s all ease;
                }

                ul .menu_sub ul li {
                    border-bottom: none;
                }

                ul .menu_sub ul li a {
                    padding: 1.29em 3.5em 1.08em;
                    display: block;
                    font-size: 2.6em;
                    text-decoration: none;
                    font-weight: 300;

                    &::before {
                        left: 2.3em;
                        bottom: 1.35em;
                        height: 1px;
                        width: 0.8em;
                    }
                }

                ul .menu_sub.active ul {
                    display: block;
                }

                ul .menu_sub.active>a::after {
                    display: none;
                }
            }

            >ul {
                padding: 60px 0 60px;
                width: 100%;

                >li {
                    background-repeat: no-repeat;
                    background-position: left 10px top 13px;
                    background-size: auto 20px;

                    >a {
                        cursor: pointer;
                        padding: 12px 20px 12px 20px;
                        display: block;
                        position: relative;
                        border-bottom: 0px dashed #fefefe;
                        font-size: 1em;
                        font-weight: 700;
                        color: #838E95;
                        text-align: center;
                        text-decoration: none;

                        span {
                            display: block;
                            font-size: 80%;
                            color: #3ba4cc;
                            letter-spacing: 2px;
                        }
                    }

                    >a {
                        &::before {
                            //   content: "";
                            position: absolute;
                            top: 50%;
                            left: 8px;
                            border: 4px solid #ffffff;
                            border-bottom-color: transparent;
                            border-right-color: transparent;
                            border-top-color: transparent;
                            transform: translateY(-50%) scaleX(1.4);
                        }
                    }
                }
            }
        }
    }

    #nav-icon {
        width: 50px;
        height: 28px;
        position: relative;
        margin: 0 auto 0px;
        cursor: pointer;
        transition: .5s ease-in-out;

        span {
            background-color: #fff;
            display: block;
            position: absolute;
            height: 2px;
            width: 100%;
            opacity: 1;
            left: 0;
            transform: rotate(0deg);
            transition: .25s ease-in-out;

            &:nth-child(1) {
                top: 2px;
            }

            &:nth-child(2), &:nth-child(3) {
                top: 11px;
            }

            &:nth-child(4) {
                top: 21px;
            }
        }

        &.open span {
            &:nth-child(1) {
                top: 11px;
                width: 0%;
                left: 50%;
            }

            &:nth-child(2) {
                transform: rotate(45deg);
            }

            &:nth-child(3) {
                transform: rotate(-45deg);
            }

            &:nth-child(4) {
                top: 11px;
                width: 0%;
                left: 50%;
            }
        }
    }

    .hamburger-btn {
        background-color: $color;
        width: 8em;
        height: 8em;
        padding: 0px 0px 0;
        position: absolute;
        bottom: 22px;
        right: 30px;
        z-index: 10;
        font-size: 1em;
        cursor: pointer;

        .button-toggle {
            position: relative;
            width: 100%;
            height: 100%;
            padding-top: 18px;
            margin: 0 auto;
            text-align: center;
            color: inherit;

            .menu_btn {
                color: #fff;
                font-family: $fen;
                font-size: 21px;
                text-align: center;
                line-height: 1;
                letter-spacing: 0px;
            }
        }
    }
}

