                    font-size: 2.6em;
                    font-weight: 300;
                    line-height: 1.846153;
                    letter-spacing: 0;
                }
            }

            .ft_content {
                padding: 0px 0 14px;
                background: none;

                .container {
                    padding: 0;
                    justify-content: center;
                }

                .menu1, .box1, .box2, .menu6 {
                    display: none;
                }

                .menu7 {
                    margin: 00px auto 9em;
                    max-width: 300px;
                }
            }
        }
    }
}

@media screen and (max-width: 750px) {
    .bottom_bar {
        display: flex;
        flex-direction: row;
        width: 100%;
        height: auto;
        top: auto;
        bottom: 0;
        opacity: 1;
        transform: translateY(100px);
        transition: all .3s;

        &.active {
            opacity: 1;
            transform: translateY(0);
        }

        .box4 {
            order: 2;
        }

        .box3 {
            order: 3;
        }

        .box2 {
            order: 1;
        }

        .box1 {
            order: 4;
        }

        >div {
            width: 25%;

            a {
                width: 100%;
                height: 90px;
                font-size: 20px;
                letter-spacing: 0.08em;
                padding-bottom: 0.2em;

                &::before {
                    height: 39px !important;
                }

                .ov {
                    display: none;
                }
            }
        }

        .box1, .box4 {
            a:before {
                background-size: auto 31px;
            }
        }

        .box2 {
            a:before {
                background-size: auto 1.35em;
            }
        }

        .box3 {
            a:before {
                background-size: auto 0.95em;
            }
        }
    }
}

@media screen and (max-width: 639px) {
    #header {
