        }

        th:first-child {
            padding-right: 1.31em;
        }
    }

    #footer .bnr .box4 a {
        padding-right: 3.9em;
        padding-top: 0;

        &::before {
            margin-right: 2.5em;
        }
    }

    .bottom_bar>div a {
        font-size: 3.1vw;
    }

    .bottom_bar .box1 a:before, .bottom_bar .box4 a:before {
        background-size: auto 1.55em;
    }
}

